/** @author Mikhail Vasin <> https://github.com/mvasin/react-div-100vh */
import React, { CSSProperties } from "react";
import { useEventListener } from "@faharmony/helpers";
const rvhRegex = /(\d+(\.\d*)?)rvh(?!\w)/;
// const containsRvh = (propertyValue: string) => rvhRegex.test(propertyValue);
const replaceRvhWithPx = (propertyStringValue: string, windowHeight: number) =>
  propertyStringValue.replace(
    rvhRegex,
    (_, rvh) => `${(windowHeight * parseFloat(rvh)) / 100}px`
  );
const throwOnBadArgs = (
  givenStyle: CSSProperties | undefined,
  windowHeight: number
) => {
  if (
    (typeof givenStyle !== "object" && givenStyle !== undefined) ||
    Array.isArray(givenStyle)
  )
    throw Error(`style (the first argument) must be an object or undefined`);
  if (typeof windowHeight !== "number" || windowHeight < 0)
    throw Error("Second argument (windowHeight) must be a non-negative number");
};
const convertStyle = (
  givenStyle: CSSProperties | undefined,
  windowHeight: number
) => {
  throwOnBadArgs(givenStyle, windowHeight);
  // If style is not passed, implicit {height: '100rvh'} style is used.
  const defaultStyle = { height: "100rvh" };
  const usedStyle = givenStyle === undefined ? defaultStyle : givenStyle;
  const convertedStyle = {};
  Object.keys(usedStyle).forEach((key) => {
    // if a value contains no rvh unit, it's used as is, otherwise converted to px; 1rvh = (window.innerHeight / 100)px
    convertedStyle[key] =
      typeof usedStyle[key] === "string"
        ? replaceRvhWithPx(usedStyle[key], windowHeight)
        : usedStyle[key];
  });
  return convertedStyle;
};
const getWindowHeight = () =>
  (document &&
    document.documentElement &&
    document.documentElement.clientHeight) ||
  window.innerHeight;

interface Div100vhProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {}

const Div100vh = (props: Div100vhProps) => {
  const [style, setStyle] = React.useState<CSSProperties>({});
  const updateStyle = () => setStyle((s) => convertStyle(s, getWindowHeight()));
  React.useEffect(() => {
    updateStyle();
  }, []);
  useEventListener("resize", updateStyle, window);
  return <div {...props} style={style} />;
};

export { Div100vh };
