import { Control } from "react-hook-form";

/** Reset form values using the form control.
 * @example const {control} = useForm();
 * <Button value="Reset" onClick={() => resetFormValues(control)} />
 */
const resetFormValues = <T extends object>(control: Control<T>) =>
  Object.keys(control.getValues()).forEach((k) =>
    control.setValue(k, undefined)
  );

export { resetFormValues };
