import React from "react";
import { useCombinedRef } from "@faharmony/helpers";

import { _IInputProps, _IUseInputFieldProps } from "./types";

const useInputField = ({
  defaultValue = "",
  ref,
  onInputClear,
}: _IUseInputFieldProps) => {
  // Refs
  const inputRef = React.useRef<HTMLInputElement>(null);
  const targetInputRef = useCombinedRef(ref, inputRef);

  // currentValue
  const [currentValue, setCurrentValue] = React.useState<string>(
    defaultValue.toString()
  );
  const [isFocussed, setFocussed] = React.useState<boolean>(false);

  React.useEffect(() => {
    const el = inputRef.current;
    if (el) {
      el.onfocus = () => setFocussed(true);
      el.onblur = () => setFocussed(false);
      el.oninput = () => setCurrentValue(el.value);
      el.onreset = () => setCurrentValue("");
      el.onchange = () => setCurrentValue(el.value);
    }
  }, [inputRef.current]);

  const isClearVisible = React.useMemo(() => currentValue !== "", [
    currentValue,
  ]);

  const handleInputClear = React.useCallback(() => {
    setCurrentValue("");
    inputRef.current && (inputRef.current.value = "");
    onInputClear && onInputClear();
  }, []);

  return {
    targetInputRef,
    isClearVisible,
    isFocussed,
    handleInputClear,
  };
};

export { useInputField };
