import React from "react";
import {
  useWatch,
  useFormContext,
  FormProvider,
  UseFormMethods,
} from "react-hook-form";
import { Text } from "@faharmony/components";
import { styled } from "@faharmony/theme";

interface IFormProps
  extends React.DetailedHTMLProps<
    React.FormHTMLAttributes<HTMLFormElement>,
    HTMLFormElement
  > {
  formMethods: UseFormMethods<any>;
}

const StyledForm = styled.form`
  /* Styles for all forms */
`;

/**
 * Form
 * ---
 * Styled wrapper for HTML form
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 */
const Form = React.forwardRef(
  (
    { formMethods, children, ...props }: IFormProps,
    ref: React.Ref<HTMLFormElement>
  ) => {
    return (
      <FormProvider {...formMethods}>
        <StyledForm {...props} ref={ref}>
          {children}
        </StyledForm>
      </FormProvider>
    );
  }
);

/** Component to render current values of the parent form. */
const WatchFormValues = () => (
  <div style={{ width: "100%", height: "100%" }}>
    <Text value="Watching form values" variant="h4" />
    <pre>
      {JSON.stringify(useWatch({ control: useFormContext().control }), null, 2)}
    </pre>
  </div>
);

export { Form, WatchFormValues };
