import React from "react";
import { Icon, Tooltip } from "@faharmony/components";
import {
  faTimesCircle,
  faExclamationCircle,
  faCheckCircle,
  faSpinner,
} from "@faharmony/icons";

import { IInputFieldProps, InputStatusType } from "./types";
import { StyledInputFieldContainer, commonClass } from "./style";
import { useInputField } from "./helpers";

/**
 * InputField
 * ---
 * Component to manage single line input.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @jira https://fasolutions.jira.com/browse/HAR-43
 * @version 1.6.0
 * @example <InputField onChange={console.log} />
 */
const InputField = React.forwardRef<HTMLInputElement, IInputFieldProps>(
  (
    {
      isClearable = true,
      disabled = false,
      readOnly = false,
      spacing = "md",
      className = commonClass,
      onInputClear,
      placeholder = "Enter value...",
      prefixIcon,
      prefixText,
      isLoading,
      status,
      statusTooltip,
      ...inputProps
    },
    ref
  ) => {
    // use helper hook
    const {
      targetInputRef,
      handleInputClear,
      isClearVisible,
      isFocussed,
    } = useInputField({ ref, onInputClear });

    // Set values while loading
    if (isLoading) {
      status = undefined;
      readOnly = true;
      statusTooltip = "Loading";
    }

    // HTML Input
    const inputField = (
      <input
        {...inputProps}
        className={className + " input"}
        disabled={disabled}
        ref={targetInputRef}
        readOnly={readOnly}
        placeholder={placeholder}
      />
    );

    // Icon to clear input value
    const showClearButton =
      isClearable && !disabled && !readOnly && isClearVisible;
    const clearButton = showClearButton && (
      <Tooltip value="Clear">
        <Icon
          icon={faTimesCircle}
          onClick={handleInputClear}
          className={className + " clearButton"}
        />
      </Tooltip>
    );

    // Field prefix
    const prefixComponent = (prefixIcon || prefixText) && (
      <div className={className + " prefix"}>
        {prefixIcon ? <Icon icon={prefixIcon} /> : prefixText || ""}
      </div>
    );

    // Field suffix
    const suffixComponent = (status || isLoading) && (
      <div className={className + " suffix"}>
        {isLoading && <Icon icon={faSpinner} spin />}
        {status === "error" && <Icon icon={faExclamationCircle} />}
        {status === "success" && <Icon icon={faCheckCircle} />}
      </div>
    );
    const suffixComponentWithTooltip = statusTooltip && suffixComponent && (
      <Tooltip value={statusTooltip || ""}>{suffixComponent}</Tooltip>
    );

    return (
      <StyledInputFieldContainer
        className={className + " inputContainer"}
        isClearable={showClearButton}
        disabled={disabled}
        spacing={spacing}
        readonly={readOnly}
        prefixIcon={prefixIcon}
        prefixText={prefixText}
        status={status}
        isFocussed={isFocussed}
      >
        {prefixComponent}
        <div className={className + " inputArea"}>
          {clearButton}
          {inputField}
        </div>
        {suffixComponentWithTooltip || suffixComponent}
      </StyledInputFieldContainer>
    );
  }
);

export { InputField, IInputFieldProps, InputStatusType };
