import { styled, Theme, getTextDimensions, SMLType } from "@faharmony/theme";

import { _IInputFieldContainerProps } from "./types";

const commonClass = "InputField";
const inputHeight = 20;

const calcHeight = ({ spacing }: _IInputFieldContainerProps) => {
  const baseHeight = 32;
  const height =
    (spacing &&
      (["lg"].includes(spacing)
        ? baseHeight + 4
        : ["xl"].includes(spacing)
        ? baseHeight + 8
        : ["sm"].includes(spacing)
        ? baseHeight - 4
        : ["xs"].includes(spacing)
        ? baseHeight - 8
        : ["none"].includes(spacing)
        ? inputHeight
        : baseHeight)) ||
    baseHeight;
  return height;
};

const prefixTextWidth = (text: string | undefined, spacing: SMLType = "md") => {
  if (!text) return 0;
  const height = calcHeight({ spacing });
  const width = getTextDimensions(text, "p").width;
  if (width < height) return height;
  else return width + 10;
};

const calcPrefixWidth = ({
  prefixIcon,
  prefixText,
  spacing,
}: _IInputFieldContainerProps) =>
  prefixIcon ? calcHeight({ spacing }) : prefixTextWidth(prefixText, spacing);

const getOutlineColor = (
  { status, isFocussed }: _IInputFieldContainerProps,
  state: string = ""
) =>
  status === "error"
    ? Theme.colorTint("danger")
    : state === "focus" || isFocussed
    ? Theme.colorText("primary")
    : Theme.colorOutline();

const getTextColor = (
  { status }: _IInputFieldContainerProps,
  state: string = ""
) =>
  status === "error"
    ? Theme.colorTint("danger")
    : state === "focus"
    ? Theme.colorText("primary")
    : state === "disabled"
    ? Theme.colorText("disabled")
    : Theme.colorText("secondary");

const getBackgroundColor = (
  { disabled, readonly }: _IInputFieldContainerProps,
  variant: string = ""
) =>
  disabled
    ? Theme.colorBackground("disabled")
    : readonly || variant === "prefix"
    ? Theme.colorBackground("base")
    : Theme.colorBackground("input");

const StyledInputFieldContainer = styled.div<_IInputFieldContainerProps>`
  width: 100%;
  min-width: 100px;
  height: ${calcHeight}px;
  border-radius: ${() => Theme.radius("sm")};
  border: 1px solid ${getOutlineColor};
  background-color: ${getBackgroundColor};
  overflow: hidden;
  display: flex;
  transition: border 0.5s;
  color: ${getTextColor};
  box-shadow: ${() => Theme.shadow("inner")};

  :hover,
  :focus {
    border-color: ${(p) => (p.disabled ? "none" : getOutlineColor(p, "focus"))};
  }

  .prefix {
    max-width: 100px;
    min-width: 20px;
    height: 100%;
    width: ${calcPrefixWidth}px;
    padding: 0 ${(p) => (p.prefixText ? Theme.spacing("sm") : 0)};
    padding-top: ${(p) =>
      (calcHeight(p) - inputHeight - (p.prefixIcon ? 2 : 0)) / 2}px;
    text-align: center;
    position: relative;
    color: ${() => Theme.colorText("secondary")};
    background-color: ${(p) => getBackgroundColor(p, "prefix")};
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .suffix {
    position: relative;
    min-width: 24px;
    width: ${calcHeight}px;
    text-align: center;
    color: ${(p) =>
      p.status === "success" ? Theme.colorTint("success") : "inherit"};
    padding-top: ${(p) => (calcHeight(p) - inputHeight - 2) / 2}px;
    background-color: transparent;
    ::before {
      content: "";
      position: absolute;
      top: 4px;
      bottom: 4px;
      left: 0;
      width: 1px;
      background-color: ${() => Theme.colorOutline()};
    }
  }

  .inputArea {
    width: 100%;
    height: 100%;
    position: relative;

    .input {
      height: ${inputHeight}px;
      border: none;
      outline: none;
      background: transparent;

      position: absolute;
      left: ${() => Theme.spacing("sm")};
      top: 50%;
      transform: translateY(-50%);
      width: calc(100% - ${(p) => (p.isClearable ? "30px" : "8px")});
      color: inherit;

      :focus {
        color: ${(p) => getTextColor(p, "focus")};
      }

      :read-only {
        color: ${getTextColor};
      }

      :disabled {
        color: ${(p) => getTextColor(p, "disabled")};
      }

      ::placeholder {
        color: ${() => Theme.colorText("disabled")};
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .clearButton {
      position: absolute;
      right: ${() => Theme.spacing("sm")};
      top: 50%;
      transform: translateY(-50%);
      color: ${() => Theme.colorText("disabled")};
      cursor: pointer;
      transition: color 0.5s;
      :hover {
        color: ${() => Theme.colorText("primary")};
      }
    }
  }
`;

export { commonClass, calcPrefixWidth, StyledInputFieldContainer };
